import React, { useMemo, useState, useEffect, useCallback } from 'react';
import { Typography, Box, CircularProgress, Alert } from '@mui/material';
import { CalendarToday, School, ArrowBack } from '@mui/icons-material';
import { useAttendance } from '../context/AttendanceContext';
import StudentItem from './StudentItem';
import AttendanceStatsComponent from './AttendanceStats';
import {
  AttendancePaper,
  HeaderSection,
  StudentListSection,
  StatsSection,
} from './styled/AttendanceContainer';
import {
  AttendancePageContainer,
  BackButton,
} from './styled/CalendarContainer';

interface AttendancePageProps {
  selectedDate: string;
  onBackToCalendar: () => void;
}

const AttendancePage: React.FC<AttendancePageProps> = ({ selectedDate, onBackToCalendar }) => {
  const { getAttendanceForDate, updateAttendance, loading, error, students } = useAttendance();
  const [attendance, setAttendance] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(true);
  
  const loadAttendanceData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await getAttendanceForDate(selectedDate);
      
      // Always show all students, not just the ones with attendance marked
      if (students && students.length > 0) {
        const allStudents = students.map(student => {
          const attendanceStudent = data.students.find(s => s.id === student.id);
          return {
            ...student,
            attendanceStatus: attendanceStudent ? attendanceStudent.attendanceStatus : 'unmarked'
          };
        });
        
        setAttendance({
          ...data,
          students: allStudents
        });
      } else {
        // Fallback to the data from API if students not loaded yet
        setAttendance(data);
      }
    } catch (err) {
      console.error('Error loading attendance data:', err);
    } finally {
      setIsLoading(false);
    }
  }, [selectedDate, students, getAttendanceForDate]);

  useEffect(() => {
    loadAttendanceData();
  }, [loadAttendanceData]);
  
  const attendanceStats = useMemo(() => {
    if (!attendance) return { total: 0, present: 0, absent: 0 };
    
    const total = attendance.students.length;
    const present = attendance.students.filter((s: any) => s.attendanceStatus === 'present').length;
    const absent = attendance.students.filter((s: any) => s.attendanceStatus === 'absent').length;
    
    return { total, present, absent };
  }, [attendance]);

  const handleAttendanceChange = async (studentId: string, status: 'present' | 'absent' | 'unmarked') => {
    try {
      await updateAttendance(selectedDate, studentId, status);
      // Reload data to get updated state
      await loadAttendanceData();
    } catch (err) {
      console.error('Error updating attendance:', err);
    }
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (loading || isLoading) {
    return (
      <AttendancePageContainer maxWidth="md">
        <AttendancePaper elevation={3}>
          <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
            <CircularProgress size={60} />
            <Typography variant="h6" sx={{ ml: 2 }}>
              Loading attendance data...
            </Typography>
          </Box>
        </AttendancePaper>
      </AttendancePageContainer>
    );
  }

  if (error) {
    return (
      <AttendancePageContainer maxWidth="md">
        <AttendancePaper elevation={3}>
          <Alert severity="error" sx={{ m: 2 }}>
            {error}
          </Alert>
          <BackButton onClick={onBackToCalendar}>
            <ArrowBack sx={{ mr: 1 }} />
            Back to Calendar
          </BackButton>
        </AttendancePaper>
      </AttendancePageContainer>
    );
  }

  if (!attendance) {
    return (
      <AttendancePageContainer maxWidth="md">
        <AttendancePaper elevation={3}>
          <Alert severity="warning" sx={{ m: 2 }}>
            No attendance data found for this date.
          </Alert>
          <BackButton onClick={onBackToCalendar}>
            <ArrowBack sx={{ mr: 1 }} />
            Back to Calendar
          </BackButton>
        </AttendancePaper>
      </AttendancePageContainer>
    );
  }

  return (
    <AttendancePageContainer maxWidth="md">
      <AttendancePaper elevation={3}>
        <BackButton onClick={onBackToCalendar}>
          <ArrowBack sx={{ mr: 1 }} />
          Back to Calendar
        </BackButton>

        <HeaderSection>
          <Box display="flex" alignItems="center" justifyContent="center" mb={2}>
            <School sx={{ fontSize: 40, color: 'primary.main', mr: 2 }} />
            <Typography variant="h4" component="h1" color="primary">
              Class Attendance
            </Typography>
          </Box>
          
          <Box display="flex" alignItems="center" justifyContent="center">
            <CalendarToday sx={{ fontSize: 24, color: 'text.secondary', mr: 1 }} />
            <Typography variant="h6" color="text.secondary">
              {formatDate(selectedDate)}
            </Typography>
          </Box>
        </HeaderSection>

        <StudentListSection>
          <Typography variant="h6" gutterBottom sx={{ mb: 2, fontWeight: 600 }}>
            Student List
          </Typography>
          {attendance.students.map((student: any) => (
            <StudentItem
              key={student.id}
              student={student}
              onAttendanceChange={handleAttendanceChange}
            />
          ))}
        </StudentListSection>

        <StatsSection>
          <Typography variant="h6" gutterBottom sx={{ mb: 2, fontWeight: 600 }}>
            Attendance Summary
          </Typography>
          <AttendanceStatsComponent stats={attendanceStats} />
        </StatsSection>
      </AttendancePaper>
    </AttendancePageContainer>
  );
};

export default AttendancePage;
