import React from 'react';
import { AttendanceStats as Stats } from '../types/attendance';
import {
  StatsContainer,
  TotalStatCard,
  PresentStatCard,
  AbsentStatCard,
  StatNumber,
  StatLabel,
} from './styled/StatsCard';

interface AttendanceStatsProps {
  stats: Stats;
}

const AttendanceStats: React.FC<AttendanceStatsProps> = ({ stats }) => {
  return (
    <StatsContainer>
      <TotalStatCard>
        <StatNumber variant="h3">{stats.total}</StatNumber>
        <StatLabel variant="body1">Total Students</StatLabel>
      </TotalStatCard>
      
      <PresentStatCard>
        <StatNumber variant="h3">{stats.present}</StatNumber>
        <StatLabel variant="body1">Present</StatLabel>
      </PresentStatCard>
      
      <AbsentStatCard>
        <StatNumber variant="h3">{stats.absent}</StatNumber>
        <StatLabel variant="body1">Absent</StatLabel>
      </AbsentStatCard>
    </StatsContainer>
  );
};

export default AttendanceStats;

