import React from 'react';
import { Student } from '../types/attendance';
import {
  StudentItemContainer,
  StudentName,
  ButtonGroup,
  TickButton,
  CrossButton,
  EditButton,
} from './styled/StudentItem';

interface StudentItemProps {
  student: Student;
  onAttendanceChange: (studentId: string, status: 'present' | 'absent' | 'unmarked') => void;
}

const StudentItem: React.FC<StudentItemProps> = ({ student, onAttendanceChange }) => {
  const [isEditMode, setIsEditMode] = React.useState(false);

  const handleTick = () => {
    onAttendanceChange(student.id, 'present');
    setIsEditMode(false);
  };

  const handleCross = () => {
    onAttendanceChange(student.id, 'absent');
    setIsEditMode(false);
  };

  const handleEdit = () => {
    setIsEditMode(true);
  };

  const handleReset = () => {
    onAttendanceChange(student.id, 'unmarked');
    setIsEditMode(false);
  };

  const isPresent = student.attendanceStatus === 'present';
  const isAbsent = student.attendanceStatus === 'absent';
  const isMarked = isPresent || isAbsent;

  return (
    <StudentItemContainer>
      <StudentName>{student.name}</StudentName>
      <ButtonGroup>
        {!isEditMode && isMarked ? (
          // Show current status with edit button
          <>
            {isPresent && (
              <TickButton variant="contained" disabled>
                ✓ Present
              </TickButton>
            )}
            {isAbsent && (
              <CrossButton variant="contained" disabled>
                ✗ Absent
              </CrossButton>
            )}
            <EditButton onClick={handleEdit}>
              ✏️ Edit
            </EditButton>
          </>
        ) : (
          // Show both buttons for selection
          <>
            <TickButton
              variant={isPresent ? 'contained' : 'outlined'}
              onClick={handleTick}
            >
              ✓ Present
            </TickButton>
            <CrossButton
              variant={isAbsent ? 'contained' : 'outlined'}
              onClick={handleCross}
            >
              ✗ Absent
            </CrossButton>
            {isEditMode && (
              <EditButton onClick={handleReset}>
                ↶ Reset
              </EditButton>
            )}
          </>
        )}
      </ButtonGroup>
    </StudentItemContainer>
  );
};

export default StudentItem;

